import { useEffect, useCallback, useState } from 'react';
import { setSession } from './utils';
import axios from 'axios';
import { endpoints } from '@/lib/axios/axios-global';
import { useAppDispatch } from '@/core/global-store';
import { setCredentials } from '@/core/global-store/features/auth/authSlice';
import Cookies from 'js-cookie';
import { useNavigate } from 'react-router-dom';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';

const STORAGE_KEY = 'accessToken';

type Props = {
    children: React.ReactNode;
};

export function AuthInitializer({ children }: Props) {
    const dispatch = useAppDispatch();
    const [isUserLoggedin, setIsUserLoggedin] = useState<boolean>(false);
    const navigate = useNavigate();

    const initialize = useCallback(async () => {
        try {
            const accessToken = Cookies.get(STORAGE_KEY);

            if (accessToken) {
                setSession(accessToken);

                const res = await axios.get(endpoints.auth.profile);
                const user = res.data;

                dispatch(setCredentials({ token: accessToken, user }));
                setIsUserLoggedin(true);
            } else {
                dispatch(setCredentials({ token: null, user: null }));
                navigate('/login');
            }
        } catch (error) {
            dispatch(setCredentials({ token: null, user: null }));
            navigate('/login');
        }
    }, []);

    useEffect(() => {
        initialize();
    }, [initialize]);

    if (!isUserLoggedin) {
        return <LoadingSpinner />;
    }

    return <>{children}</>;
}
